
<template>
  <div>
    <div class="tittle">
      <h2>UPCOMING</h2>
      <h3> The most fire releases of the year</h3> 
    </div> 

      <div class="container-md">
          <b-card-group columns>
            <div columns v-for="proyecto in proyectos" :key="proyecto.id">
             <Card2 :autor="proyecto.autor" :srcimg="proyecto.srcimg" :alt="proyecto.alt" :titulo="proyecto.titulo" :texto="proyecto.texto" :url="proyecto.url"></Card2> 
            </div>
           </b-card-group>
       </div>



 </div>

</template>

<script>
import Card2 from '../components/Card2.vue' ;
const lstProysJson = '../proyectosWD2021.json';


export default {
  props: {  
   name: {
      type: String,
      default: 'UpComing'
    }
  },
  components: {
        Card2
      },
  data() {
      return {
        proyectos:[],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 5
      }
    },
    mounted() {
      this.fetchData()
    },
    methods: {
        async fetchData() {
          const res = await fetch (lstProysJson);
          const val= await res.json();
          this.proyectos=val;
          console.log(val);
        }
        
    
    }
  }
</script>

